import  Component  from 'react';
import styles from "./logo.module.css"; 
import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

const ConcentricHexagons = ({ sideLength }) => {
    const svgRef = useRef(null);
    const width = sideLength * 6;
    const height = sideLength * Math.sqrt(3);

  useEffect(() => {
    const svg = d3.select(svgRef.current);

    // Clear any existing content
    svg.selectAll('*').remove();

    // Calculate the necessary values
    
    const centerX = width / 2;
    const centerY = height / 2;
    const outerRadius = sideLength;
    const innerRadius = sideLength / 2;

    // Create a group element to hold the hexagons
    const hexagonGroup = svg.append('g').attr('transform', `translate(${centerX}, ${centerY})`);

    // Define the hexagon generator
    const hexagonGenerator = d3
      .line()
      .x((d, i) => outerRadius * Math.cos((2 * Math.PI * i) / 6))
      .y((d, i) => outerRadius * Math.sin((2 * Math.PI * i) / 6))
      .curve(d3.curveLinearClosed);

    // Draw the outer hexagon
    hexagonGroup
      .append('path')
      .attr('d', hexagonGenerator(d3.range(6)))
      .attr('stroke', 'white')
      .attr('stroke-width', 1);

    // Draw the inner hexagon
    hexagonGroup
      .append('path')
      .attr('d', hexagonGenerator(d3.range(6)))
      .attr('stroke', 'white')
      .attr('stroke-width', 1)
      .attr('transform', `scale(${innerRadius / outerRadius})`);

    

  }, [sideLength]);

  return <svg ref={svgRef} className={styles.svg} width={width} height={height} overflow="visible"/>;
};

export default ConcentricHexagons;
  